import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PickListNewComponent } from './pick-list-new.component';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { NgSelectModule } from '@ng-select/ng-select';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PickListNewService } from 'src/app/service/pick-list-new.service';

const routes: Routes = [
  {
    path: '',
    component: PickListNewComponent,
    data: { title: 'pick_list_list' },
  },
  {
    path: 'add',
    loadChildren: () => import('./add-picklist/add-picklist.module').then(m => m.AddPicklistModule),
    data: { title: 'add_pick_list' },
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('./add-picklist/add-picklist.module').then(m => m.AddPicklistModule),
    data: { title: 'edit_pick_list' },
  },
  {
    path: 'view/:id',
    loadChildren: () => import('./pick-list-view/pick-list-view.module').then(m => m.PickListViewModule),
    data: { title: 'view_pick_list' },
  },
  {
    path: 'pick-view/:id',
    loadChildren: () => import('./pick-list-view-product/pick-list-view-product.module').then(m => m.PickListViewProductModule),
    data: { title: 'view_pick_list' },
  }

]


@NgModule({
  declarations: [PickListNewComponent],
  imports: [
    CommonModule,
    FormsModule,
    NgSelectModule,
    RouterModule.forChild(routes),
    SearchModule,
    BtnAddModule,
    PaginationModule,
    TableListModule,
  ], providers: [PickListNewService]
})
export class PickListNewModule { }
